<template>
  <div class="text-center">
    <form class="form-signin">
      <h1 class="h3 mb-3 font-weight-normal">
        Please sign in
      </h1>
      <label for="inputEmail" class="sr-only">
        Email address
      </label>
      <input type="email" id="inputEmail" class="form-control"
        placeholder="Email address" required autofocus v-model="user.username">
      <label for="inputPassword" class="sr-only">
        Password
      </label>
      <input type="password" id="inputPassword" class="form-control"
        placeholder="Password" required v-model="user.password">
      <button class="btn btn-lg btn-primary btn-block" type="submit"
      @click.prevent="checkAccount">
        Sign in
      </button>
      <p class="mt-5 mb-3 text-muted">
        &copy; 2017-2018
      </p>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: {
        username: '',
        password: '',
      },
    };
  },
  methods: {
    checkAccount() {
      const api = `${process.env.API_PATH}/admin/signin`;
      const vm = this;
      this.$http.post(api, vm.user).then((response) => {
        if (response.data.success) {
          vm.$router.push('/admin');
        } else {
          this.$bus.$emit('showError', response.data.message);
        }
      });
    },
  },
  created() {

  },
};
</script>

<style scoped>
html,
body {
  height: 100%;
}

body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 40px;
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
}
.form-signin .checkbox {
  font-weight: 400;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type='email'] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type='password'] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
</style>



